import React, { Component, useEffect, useState } from "react";
import { Link, useSearchParams } from "react-router-dom";
import Clipboard from "@react-native-clipboard/clipboard";

import axios from "axios";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { solid, brands } from '@fortawesome/fontawesome-svg-core/import.macro';

import './assets/css/build.css';

const numberFormat = (num) => {
  return new Intl.NumberFormat('en-US').format(num);
}

const Character = (props) => {
  let [searchParams, setSearchParams] = useSearchParams();
  let [charData, setCharData] = useState({
    "stat": {
      "HP": "0",
      "ATK": "0",
      "DEF": "0",
      "EM": "0",
      "ER": "0.0",
      "CR": "0.0",
      "CDM": "0.0"
    },
    "artifacts": {}
  });

  let [status, setStatus] = useState(0);
  let charName = searchParams.get("name").toLocaleLowerCase();

  useEffect(() => {
    async function getBuild() {
      try {
        const res = await axios.get(`./assets/json/${charName}.json`);
        setCharData(res.data);
        document.title = `Irvan789 | ${res.data.name}`;
        setStatus(200);
      } catch (err) {
        document.title = `Irvan789 | 404 Not Found`;
        setStatus(404);
      }
      props.stopLoading();
    }

    if (charName) {
      getBuild()
    }
  }, [props, charName])

  const searchBuild = (event) => {
    event.preventDefault();
    let formData = new FormData(event.currentTarget);
    let newChar = formData.get("name").replace(/\W/g, '_');
    if (!newChar) {
      return
    } else {
      setStatus(0);
      setSearchParams({ name: newChar });
    }
  }

  const copyLinkBuild = (event) => {
    event.preventDefault();
    Clipboard.setString(window.location.href);
    window.alert("Copy Link Successfully!")
  }

  class ViewTitleLoad extends Component {
    render() {
      return (
        <>
          <div className="character-top load404">
            <div className="character-name-level">
              <p className="character-name">
                Loading &#x231B;
              </p>
            </div>
          </div>
        </>
      )
    }
  }

  class ViewMainLoad extends Component {
    render() {
      return (
        <div className="row">
          <div className="col-12 text-center fw-bold">
            <div className="fs-4">
              Waiting For Response &#x231B;
            </div>
          </div>
        </div>
      )
    }
  }

  class ViewTitle404 extends Component {
    render() {
      return (
        <>
          <div className="character-top load404">
            <div className="character-name-level">
              <p className="character-name">
                404 Not Found
              </p>
            </div>
          </div>
        </>
      )
    }
  }

  class ViewMain404 extends Component {
    render() {
      return (
        <div className="row">
          <div className="col-12 text-center fw-bold">
            <div className="fs-4">
              Oops, Karakter "{(charName).replace(/\W|_/g, " ")}" Tidak Di Temukan!
            </div>
          </div>
        </div>
      )
    }
  }

  class ViewTitle200 extends Component {
    render() {
      return (
        <div className="character-top">
          <div className="character-icon" style={{ backgroundImage: `url('./assets/img/character/${charData.region}/${charData.iconName}')` }}></div>
          <img className="character-icon-border" alt="borderImg" src="./assets/img/border.png" />
          <div className="character-name-level">
            <p className="character-name">
              {charData.name}
            </p>
            <p className="character-level">
              Level {charData.level} C{charData.cons}
            </p>
          </div>
        </div>
      )
    }
  }

  class ViewMain200 extends Component {
    render() {
      return (
        <div className="row" style={{ padding: "0rem 0.75rem" }}>
          <div className={`col-12 build build-${charName} shadow-sm`}>
            <div className="d-stat me-auto">
              <table className="table table-stat table-borderless mb-0">
                <tbody>
                  <tr>
                    <td>
                      Max HP
                    </td>
                    <td>
                      {numberFormat(charData.stat.HP)}
                    </td>
                  </tr>
                  <tr>
                    <td>
                      ATK
                    </td>
                    <td>
                      {numberFormat(charData.stat.ATK)}
                    </td>
                  </tr>
                  <tr>
                    <td>
                      DEF
                    </td>
                    <td>
                      {numberFormat(charData.stat.DEF)}
                    </td>
                  </tr>
                  <tr>
                    <td>
                      Elemental Mastery
                    </td>
                    <td>
                      {numberFormat(charData.stat.EM)}
                    </td>
                  </tr>
                  <tr>
                    <td>
                      Energy Recharge
                    </td>
                    <td>
                      {charData.stat.ER}%
                    </td>
                  </tr>
                  <tr>
                    <td>
                      Crit Rate / Crit DMG
                    </td>
                    <td>
                      {charData.stat.CR}% / {charData.stat.CDM}%
                    </td>
                  </tr>
                  <tr>
                    <td>
                      Talent Level
                    </td>
                    <td>
                      {charData.talents}
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
          <div className="col-12 p-0">
            <h3 className="fw-bold text-center mt-4 mb-4">
              Weapon &#38; Artifact
            </h3>
            <div className="row row-card row-cols-2 row-cols-sm-3 g-0 m-auto">
              {
                Object.entries(charData.enquipment).map((v, i) => {
                  return (
                    <div className="col" key={i}>
                      <div className="card">
                        <img src={v[1]} className="card-img-top" alt={v[0]} />
                      </div>
                    </div>
                  )
                })
              }
            </div>
          </div>
        </div>
      )
    }
  }

  return (
    <>
      <section id="CharacterBuild" className="d-flex align-items-center">
        <div className="container-fluid">
          <div className="row">
            <div className="col-12 p-3">
              <div className="character">
                <div className="character-border">
                  <div className="character-body">
                    <div className="character-menu">
                      <Link onClick={() => props.startLoading()} to="../" className="menu-class menu-home">
                        <FontAwesomeIcon icon={solid('house')} />
                      </Link>
                      <a href="#copyLink" onClick={copyLinkBuild} className="menu-class menu-home">
                        <FontAwesomeIcon icon={solid('link')} />
                      </a>
                      <a href="https://github.com/Irvan789/GenshinBuild" className="menu-link" target="_blank" rel="noreferrer">
                        <FontAwesomeIcon icon={brands('github')} />
                      </a>
                    </div>
                    <div className="character-main">
                      {(status === 0 ? <ViewTitleLoad /> : (status === 200 ? <ViewTitle200 /> : <ViewTitle404 />))}
                      <div className="character-search">
                        <form onSubmit={searchBuild} className="ms-auto text-right" autoComplete="off" style={{ maxWith: "200px" }}>
                          <div className="input-group mb-0">
                            <input type="text" className="form-control" placeholder="Find Character"
                              name="name" />
                            <button className="btn text-white">
                              <FontAwesomeIcon icon={solid('magnifying-glass')} />
                            </button>
                          </div>
                        </form>
                      </div>
                      <main>
                        {(status === 0 ? <ViewMainLoad /> : (status === 200 ? <ViewMain200 /> : <ViewMain404 />))}
                      </main>
                      <div className="character-copyright">
                        &copy; {new Date().getFullYear()} | CrunchyNuts 800171178
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  )
}

export default Character;